export default {
    layout: "Profile-content",
    data() {
        return {

            qualification:
            {
                degree_name: "",
                subject_name: "",
                passing_year: "",
                institute_name: "",

            },
            errors: '',

        };
    },
    methods: {
        addQualification() {
            this.$axios
                .$post("/qualification/add", this.qualification)
                .then((res) => {
                    this.$router.push("/profile/qualification");
                    this.qualification = "",
                    this.errors = "",
                    this.$toaster.success(res.message);
                    this.getqualification();
                    
                })
                .catch((err) => {                    
                    this.errors = err.response.data.errors;

                });
        },

    }






};
